<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/ossCloudStorage"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt>
        <li>带 * 为必填项</li>
      </cms-prompt>
       <el-form ref="form" :rules="updateRules" :model="formData" label-width="210px">
        <el-form-item label="名称" prop="ossName" :error='errorMessage.ossName'>
          <el-input v-model="formData.ossName" :autofocus="true" ref="first" maxlength="200"></el-input>
        </el-form-item>
        <el-form-item label="访问域名" prop="accessDomain"  :error='errorMessage.accessDomain' >
          <el-input v-model="formData.accessDomain" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="APPID" prop="appId"  :error='errorMessage.appId' >
          <el-input v-model="formData.appId" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="key或者secret" prop="appKey"  :error='errorMessage.appKey' >
          <el-input v-model="formData.appKey"  maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="地区码" prop="bucketArea" :error='errorMessage.bucketArea'>
          <el-input v-model="formData.bucketArea"  maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="bucket名" prop="bucketName" :error='errorMessage.bucketName'>
          <el-input v-model="formData.bucketName"  maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="end_point" prop="endPoint" :error='errorMessage.endPoint'>
          <el-input v-model="formData.endPoint"  maxlength="100"></el-input>
        </el-form-item>
          <el-form-item label="存储类型" prop="ossType">
          <el-radio-group v-model="formData.ossType">
            <el-radio :label="1">腾讯云cos</el-radio>
            <el-radio :label="2">阿里云oss</el-radio>
            <el-radio :label="3">七牛云</el-radio>
          </el-radio-group>
      </el-form-item>
      <el-form-item label="secret_id" prop="secretId" :error='errorMessage.secretId'>
          <el-input v-model="formData.secretId"  maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button @click="update"  v-color >保存修改</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  occGet,
  occEdit
} from '@/api/setting/occCloudStorage.js'

var defaultData = {
  'accessDomain': '',
  'appId': '',
  'appKey': '',
  'bucketArea': '',
  'bucketName': '',
  'endPoint': '',
  'ossName': '',
  'ossType': 1,
  'secretId': ''
}
export default {
  name: 'adminEdit',
  mixins: [form],
  data() {
    var validateSecret = (rule, value, callback) => { // 验证secretId
      const reg = /^\w+$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error('只能输入字母，数字,下划线'))
      } else {
        callback()
      }
    }
    return {
      roleList: [],
      formData: Object.assign({}, defaultData),
      updateRules: {// 修改规则
        accessDomain: [rules.required('访问域名不能为空'), rules.url()],
        ossName: [rules.required('名称不能为空')],
        appKey: [rules.required('appKey不能为空')],
        ossType: [rules.required('存储类型不能为空')],
        secretId: [rules.custom(validateSecret)]
      }
    }
  },
  methods: {
    getFormData(id) { // 通过id获取表单信息
      occGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    update() {
      this.updateForm(occEdit, this.formData, '/ossCloudStorage/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>




